.home {
    padding: 0 25px 0;
    margin-bottom: 40px;
}

.home>h3 {
    text-align: center;
}

.home>.box-full {
    border: 1px dashed rgb(194, 194, 194);
    padding-right: 10px;
    margin-top: 20px;
    text-align: center;
    position: relative;
}

.home>.box-full>.box-title {
    color: rgb(125, 70, 52);
    font-size: 25px;
    font-family: arial;
    font-weight: bold;
}

.home>.box-banner {
    margin-top: 15px;
    background-image: url(../images/banner.webp);
    background-size: cover;
    min-width: 600px;
    min-height: 450px;
    max-width: auto;
    max-height: auto;
    padding: 10px;
    border-radius: 10px;
    cursor: default;
    /* user-select: none; */
}

@media only screen and (max-width: 680px) {
    .home>.box-banner {
        min-width: unset;
        min-height: unset;
        width: auto;
        height: auto;
    }
}

.home>.box-banner>.box-small {
    font-size: 14px;
    background-color: #ffffffe0;
    border-radius: 10px;
    padding: 10px;
    text-align: right;
    margin-bottom: 10px;
}

.home>.box-banner>.box-small>div:first-child>b {
    font-size: 16px;
}